*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

p:last-child {
  margin-bottom: 0;
}

ul, ol, a {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.hiddenvisualy {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

button {
  text-align: center;
  cursor: pointer;
  border-radius: 100px;
  margin: 0;
  padding: 0;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.21429;
  list-style: none;
}

:root {
  --light-theme-fill: #f8f8f8;
  --light-theme-main-text: #050505;
  --light-theme-switcher-btn: #cecdcd;
  --dark-theme-fill: #161616;
  --dark-theme-main-text: #fff;
  --dark-theme-switcher-btn: #f3f3f3;
  --green-btn: #9bb537;
  --star-primal: #eea10c;
  --star-second-light: #d9d9d9;
  --star-second-dark: #3c3b3b;
  --transition-dur-and-func: 250ms cubic-bezier(.4, 0, .2, 1);
}

body {
  color: var(--light-theme-main-text);
  background-color: var(--light-theme-fill);
  font-family: Inter, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.28;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 768px) {
  .container {
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    padding-left: 100px;
    padding-right: 100px;
  }
}

@media screen and (min-width: 375px) {
  .container-mobile {
    max-width: 200px;
    min-height: 100vh;
    margin-left: auto;
    margin-right: auto;
  }
}



.hero-wrapper {
  width: 335px;
  text-align: left;
  margin-bottom: 48px;
}

.hero-title {
  color: var(--light-theme-main-text);
  text-transform: uppercase;
  margin-bottom: 14px;
  font-size: 26px;
  font-weight: 600;
  line-height: 31.2px;
}

.hero-title span {
  color: var(--green-btn);
  font-style: italic;
  font-weight: 400;
}

.hero-description {
  color: var(--light-theme-main-text);
  margin-bottom: 28px;
  font-size: 14px;
  line-height: 18px;
}

.hero-btn {
  color: var(--light-theme-main-text);
  border: 1px solid var(--green-btn);
  background: none;
  border-radius: 12px;
  padding: 10px 20px;
  font-size: 14px;
  font-weight: 500;
}

.hero-btn:hover, .hero-btn:focus {
  background-color: var(--green-btn);
  border: 1px solid var(--green-btn);
  color: var(--light-theme-fill);
  transition: var(--transition-dur-and-func);
}

@media screen and (min-width: 768px) {
  .hero-wrapper {
    width: 446px;
    margin-bottom: 64px;
  }

  .hero-title {
    margin-bottom: 16px;
    font-size: 36px;
    line-height: 43.2px;
  }

  .hero-description {
    width: 408px;
    margin-bottom: 32px;
    font-size: 16px;
    line-height: 24px;
  }

  .hero-btn {
    border-radius: 15px;
    padding: 14px 28px;
  }
}

@media screen and (min-width: 1280px) {
  .hero-wrapper {
    margin-bottom: 0;
  }

  .hero-description {
    width: 400px;
  }
}

.categories-main-btn {
  transition: var(--transition-dur-and-func);
  background: none;
  border: 1px solid rgba(5, 5, 5, .2);
  border-radius: 15px;
  margin-bottom: 24px;
  padding: 14px 29px;
}

@media screen and (min-width: 768px) {
  .categories-main-btn {
    margin-bottom: 30px;
  }
}

.categories-main-btn:-webkit-any(:hover, :focus) {
  background: var(--green-btn);
  border-color: rgba(0, 0, 0, 0);
}

.categories-main-btn:is(:hover, :focus) {
  background: var(--green-btn);
  border-color: rgba(0, 0, 0, 0);
}

.categories-main-btn:is(:hover, :focus) .categories-main-btn-text {
  color: var(--light-theme-fill);
  opacity: 1;
}

.categories-main-btn-text {
  color: var(--light-theme-main-text);
  opacity: .5;
  transition: var(--transition-dur-and-func);
  font-size: 18px;
  font-weight: 500;
}

.categories-list-container {
  height: 198px;
  overflow-y: scroll;
}

.categories-list-container::-webkit-scrollbar {
  width: 8px;
}

.categories-list-container::-webkit-scrollbar-thumb {
  background-color: rgba(5, 5, 5, .18);
  border-radius: 12px;
}

@media screen and (min-width: 768px) {
  .categories-list-container {
    width: 176px;
    height: 416px;
  }
}

@media screen and (min-width: 1280px) {
  .categories-list-container {
    width: 235px;
  }
}

.categories-list-btn {
  color: var(--light-theme-main-text);
  opacity: .3;
  flex-direction: column;
  gap: 18px;
  font-weight: 500;
  display: flex;
}

@media screen and (min-width: 768px) {
  .categories-list-btn {
    gap: 24px;
    font-size: 16px;
    line-height: 1.25;
  }
}

.categories-item {
  border: none;
}

.categories-item.active-category {
  color: var(--green-btn);
  opacity: 1;
}

.categories-list-item {
  transition: var(--transition-dur-and-func);
}

.categories-list-item:-webkit-any(:hover, :focus) {
  color: var(--green-btn);
  opacity: 1;
}

.categories-list-item:is(:hover, :focus) {
  color: var(--green-btn);
  opacity: 1;
}





.card-template {
  width: 250px;
  height: 287px;
  background: linear-gradient(1deg, rgba(5, 5, 5, .6) 0%, rgba(5, 5, 5, 0) 100%), #d3d3d3 -36.5px 0 / 129.2% 112.544% no-repeat;
  border: 1px solid #000;
  border-radius: 8px;
  flex-shrink: 0;
  margin-bottom: 20px;
  padding: 16px;
  position: relative;
  overflow: hidden;
}

.card-template-img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.card-template-info {
  flex-direction: column;
  justify-content: end;
  display: flex;
  position: absolute;
}

.card-template-title {
  text-transform: uppercase;
  color: var(--light-theme-fill);
  font-size: 14px;
  font-weight: 600;
  line-height: 1.28;
}

.card-template-descr {
  color: var(--light-theme-fill);
  font-size: 12px;
  font-weight: 400;
  line-height: 1.33;
  overflow: hidden;
}



















